import AdminLayout from "@/layouts/Admin/index.vue";
import { defineComponent } from "vue";
import routes from "@/routes";
import { mapActions } from "vuex";
import { Boto } from "@/interfaces/Boto";
import BotCardPlaceHolder from "@/components/PlaceHolders/BotCardPlaceHolder/index.vue";
import Loading from "vue3-loading-overlay";
import "vue3-loading-overlay/dist/vue3-loading-overlay.css";
import { HttpRequestMethod } from "@/enums/HttpRequestMethod";
import HttpClient from "@/util/HttpClient";
import { v4 as uuidv4 } from "uuid";

const items: Boto[] = [];

export default defineComponent({
  name: "Recipes",
  components: {
    AdminLayout,
    BotCardPlaceHolder,
    Loading,
  },
  created() {
    if (!navigator.onLine) {
      routes.push({
        name: "home.offline",
        params: { route: this.$route.fullPath },
      });
    }
  },
  data() {
    return {
      items,
      isLoadingData: false,
      isLoadingTemplateData: false,
      errorText: "",
      fullPage: true,
      error: false,
    };
  },
  async mounted() {
    try {
      this.isLoadingData = true;
      const templates = await this.getTemplates();
      this.items = templates;
    } catch (e) {
      this.error = true;
      this.errorText = HttpClient.getTextError(e);
    } finally {
      this.isLoadingData = false;
    }
  },
  methods: {
    ...mapActions("editor", ["setTemplate"]),
    async getTemplates() {
      return await HttpClient.request(
        `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/bot_templates/`,
        null,
        HttpRequestMethod.GET
      );
    },
    async getTemplate(templateId: string) {
      return await HttpClient.request(
        `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/bot_template/${templateId}/`,
        null,
        HttpRequestMethod.GET
      );
    },
    useTemplate(id: string) {
      // this.isLoadingTemplateData = true;
      // if we are going to fetch it, why not just fetch it in the editor?
      // const template = await this.getTemplate(id);
      // delete template.id;
      // this.setTemplate(template);
      // console.log();
      this.$router.push({
        path: "editor",
        query: { templateId: id, type: "recipe" },
      });
    },
  },
});
